<template>
  <div class="main">
    <div class="retbtn" @click="retClick">
      <span class="iconfont icon-zuo"></span>
    </div>
    <div class="addresslist" v-for="item in list" :key="item.id">
      <div class="address" @click="chooseClick(item)">
        <div class="iconleft">
          <span class="iconfont icon-ditu_dingwei_o"></span>
        </div>

        <div class="address-text">
          <div class="name">
            {{ item.name }} <span class="phone">{{ item.tel }}</span>
          </div>
          <div class="detail">
            {{ item.province + item.city + item.county + item.addressDetail }}
          </div>
        </div>
        <div class="iconyou"></div>
      </div>
      <div class="addressbtn">
        <div class="addressbtn-left">
          <van-checkbox
            :value="item.isDefault"
            icon-size="18px"
            checked-color="#4b4a52"
            @click="changeDefault(item)"
            >默认地址</van-checkbox
          >
        </div>
        <div class="addressbtn-right">
          <div class="editbtn" @click="edit(item)">
            <span class="iconfont icon-bianji"></span>
            <span>编辑</span>
          </div>
          <div class="delbtn" @click="del(item)">
            <span class="iconfont icon-lajitong"></span>
            <span>删除</span>
          </div>
        </div>
      </div>
    </div>
    <div class="addbtn" @click="gotoAddressEdit">新建收货地址</div>
  </div>
</template>

<script>
import mixin from "@/mixin";
import { mapState, mapActions,mapMutations } from "vuex";
export default {
  mixins: [mixin],
  data() {
    return {
      checked: false,
    };
  },
  computed: {
    ...mapState("address", ["list"]),
  },
  methods: {
    ...mapActions("address", ["initList", "delList", "updateList"]),
    ...mapMutations("address",["setChoose"]),
    retClick() {
      this.setChoose()
      this.$router.go(-1);
    },
    chooseClick(item){
      this.setChoose(item)
      this.$router.go(-1)
    },
    gotoAddressEdit() {
      this.$router.push("/addressedit");
    },
    del(item) {
      this.$dialog
        .confirm({
          title: "是否删除？",
        })
        .then(() => {
          this.delList(item.id);
        })
        .catch(() => {});
    },
    edit(item) {
      this.$router.push("/addressedit?id=" + item.id);
    },
    changeDefault(item) {
      if (!item.isDefault) {
        this.updateList({ ...item, isDefault: true }).then(() => {
          this.$toast("设置成功");
        });
      } else {
        this.updateList({ ...item, isDefault: false }).then(() => {
          this.$toast("设置成功");
        });
      }
    },
  },
  created() {
    this.initList(this.$store.state.userinfo.id);
  },
};
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 50px;
}

.retbtn {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 30px;
  height: 30px;
  background: rgba($color: #999, $alpha: 0.8);
  text-align: center;
  color: #fff;
  line-height: 30px;
  font-size: 14px;
  border-radius: 15px;
  .icon-zuo {
    padding-left: 7px;
    font-size: 18px;
  }
}
.addresslist {
  margin-bottom: 10px;
}
.address {
  width: 100%;
  min-height: 60px;
  background-color: #fff;
  display: flex;
  border-bottom: 0.5px solid #e8e8e8;
  .iconleft {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70px;
    .icon-ditu_dingwei_o {
      font-size: 36px;
      color: #6c6c6c;
    }
  }
  .iconyou {
    width: 30px;
  }
  .address-text {
    flex: 1;
    padding: 12px 0 8px 0;
    overflow: hidden;
    .name {
      width: 100%;
      height: 18px;
      font-size: 16px;
      line-height: 18px;
      color: #363131;
      font-weight: bold;
      display: flex;
      .phone {
        flex: 1;
        margin-left: 16px;
        overflow: hidden;
      }
    }
    .detail {
      width: 100%;
      line-height: 18px;
      margin-top: 6px;
      color: #6c6c6c;
      font-size: 14px;
    }
  }
}
.addressbtn {
  padding: 10px 20px;
  box-sizing: border-box;
  width: 100%;
  height: 38px;
  background: #fff;
  display: flex;
  align-items: center;
  .addressbtn-left {
    height: 100%;
    flex: 1;
  }
  .addressbtn-right {
    margin-left: 50px;
    display: flex;
    line-height: 18px;
    height: 100%;
    color: #6c6c6c;
    .iconfont {
      margin-right: 7px;
      color: #6c6c6c;
    }
    .editbtn {
      margin-right: 22px;
    }
  }
}
.addbtn {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: center;
  line-height: 50px;
  font-size: 16px;
  color: #fff;
  background: #e84040;
}
</style>